<template>
    <div class="NItemDetails">
      <div class="position">
        <div class="position-warp">
          <span></span>  <span>位置:政企智慧服务平台</span> &nbsp;<span> > </span>&nbsp; <span>企业需求征集系统</span> &nbsp;<span> > </span>&nbsp; <span class="need-ting">需求大厅</span>&nbsp;<span> > </span>&nbsp;<span style="color: #555555;">需求详情</span>
        </div>
        <div class="info">
          <div class="info-l">
            <div class="info-l1">
              <span>{{info.demandOutline}}</span><span>阅读(2347)</span>
            </div>
            <div class="info-l2" v-if="info">
              截止时间：{{info.endDate.substring(0,10)}}
            </div>
            <div class="info-l3">
              <span>预投金额：</span><span>{{info.preInvestmentAmount}}万</span>
            </div>
            <div class="info-l4">
              <ul>
                <li>
                  <div>{{info.demandIndustry}} <br> {{info.demandIndustryDetail}}</div>
                  <div>需求行业</div>
                </li>
                <li>
                  <div>{{info.cooperationType}}</div>
                  <div>合作方式</div>
                </li>
                <li>
                  <div>{{info.demandType}}</div>
                  <div>需求类别</div>
                </li>
                <li>
                  <div v-if="info.cooperationIntention">{{info.cooperationIntention}}</div>
                  <div v-else>无</div>
                  <div>合作倾向</div>
                </li>
              </ul>
            </div>
            <div  class="consultation" style="position: relative">
              <span @click="chat">立即咨询</span>
               <div class="share">
                 <i>分享至</i>
                 <img src="../assets/wx.png" alt=""><img src="../assets/wb.png" alt="">    <img src="../assets/qqzone.png" alt="" >
               </div>
            </div>
          </div>
          <div class="info-r">
            <div class="info-r1">需求企业</div>
            <div class="info-r2">
              <div class="info-r-1"><span>平台认证</span><span>{{info.cooperationIntention}}</span></div>
              <div  class="info-r-2" >
                <img :src="info.logo" alt="">
              </div>
              <div class="info-r-3" >{{info.companyName}}</div>
              <div class="info-r-4" ><img src="../assets/position1.png" alt="" class="position1">{{info.province + info.city + info.district}}</div>
            </div>
          </div>
        </div>
        <div class="infoDetails">
              <div class="detail-head">
                <span :class="{'active2':isDetails}" @click="totabs(1)">需求详情</span><span @click="totabs(2)" :class="{'active2':!isDetails}">期望实现结果</span>
              </div>
              <div class="detail-content" v-show="isDetails">
                <div>
                   {{info.demandContent}}
                </div>
              </div>
          <div class="detail-content" v-show="!isDetails">
            <div>
              {{info.expectedResult}}
            </div>
          </div>
        </div>
      </div>

    </div>
</template>

<script>
    export default {
        name: "NItemDetails",
        data(){
            return {
                isDetails:true,
                info:''
            }
        },
        created(){
            this.getInfo();
        },
        methods:{
            totabs(i){
              if (i==1){
                this.isDetails = true;
              }
                if (i==2){
                    this.isDetails = false;
                }
            },
            //获取详情信息
            getInfo(){
                var id = this.$route.query.id;
                console.log(id);
                var that = this;
                this.$axios.get('/demands/getDemandsByID',{
                    params:{
                        id:id
                    }
                })
                    .then(res=>{
                        if (res.data.code==0){
                            that.info = res.data.data;
                        }
                        console.log(res)
                    })
            },
            chat(){
                this.$alert('功能开发中','提醒', {
                    confirmButtonText: '确定',
                });
            }
        }
    }
</script>

<style scoped lang="less">
.NItemDetails {
  width: 100%;
  height: 1117px;
  background-color: #f7f7f7;
  .position {
    height: 40px;
    background-color: #f7f7f7;

    .position-warp {
      width: 1200px;
      height: 100%;
      margin: 0 auto;
      line-height: 40px;
      color: #8a8a8a;
      font-size: 12px;
      position: relative;
      span:nth-child(1){
        display: inline-block;
        width: 13px;
        height: 19px;
        background: url("../assets/position.png");
        background-size: 100% 100%;
        margin-top: 3px;
        position: absolute;
        top: 50%;
        transform: translateY(-71%);
      }
      span:nth-child(2){
        display: inline-block;
        padding-left: 24px;
      }
    }

  }
}
  .info {
    width: 1200px;
    margin: 8px auto;
    height: 347px;

    .info-l {
      width: 918px;
      height: 347px;
      float: left;
      background-color: #ffffff;
      border: solid 1px #bcbcbc;
      box-sizing: border-box;
      padding: 24px 35px 28px 56px;
      .info-l1 {
         span:nth-child(1){
           color: #3a3a3a;
           font-size: 24px;
         }
        span:nth-child(2){
          color: #bebebe;
          font-size: 14px;
          float: right;
        }
      }
      .info-l2 {
        color: #3a3a3a;
        font-family: PingFangSC-Regular;
        font-size: 16px;
        margin-top: 14px;
      }
      .info-l3 {
        width: 826px;
        height: 72px;
        background-color: #f6f6f6;
        line-height: 72px;
        box-sizing: border-box;
        padding-left: 13px;
        margin-top: 14px;
        span:nth-child(1){
          color: #232323;
          font-size: 18px;
        }
        span:nth-child(2){
          font-family: PingFangSC-Semibold;
          font-size: 26px;
          font-weight: normal;
          letter-spacing: 0px;
          color: #008fff;
        }
      }
      .info-l4 {
         ul{
           margin-top: 27px;
           li {
             list-style: none;
             display: inline-block;
             box-sizing: border-box;
             vertical-align: top;
             max-width: 230px;
             div:nth-child(1){
               color: #3a3a3a;
               font-size: 16px;
             }
             div:nth-child(2){
               color: #7f7f7f;
               font-size: 14px;
               margin-top: 9px;
             }
           }
           li:nth-child(1){
             padding-left: 14px;
             padding-right: 16px;
             height: 45px;
             border-right: 1px solid #e9e9e9;
             max-width: 230px;
           }
           li:nth-child(2){
             padding-left: 44px;
             padding-right: 45px;
             height: 45px;
             border-right: 1px solid #e9e9e9;
           }
           li:nth-child(3){
             padding-left: 74px;
             padding-right: 85px;
             height: 45px;
             border-right: 1px solid #e9e9e9;
           }
           li:nth-child(4){
             padding-left: 55px;
             height: 45px;
           }
         }
      }
    }
    .info-r {
      width: 263px;
      height: 347px;
      float: right;
      background-color: #ffffff;
      border: solid 1px #bcbcbc;
       .info-r1 {
         height: 50px;
         width: 100%;
         border-bottom: 1px solid #bcbcbc;
         background-color: #f4f4f4;
         color: #5e5e5e;
         font-size: 18px;
         line-height: 50px;
         text-align: center;
         font-weight: 600;
         font-stretch: normal;
       }
    }
  }
  .consultation {
    margin-top: 35px;
         span {
           display: inline-block;
           width: 217px;
           height: 44px;
           background-color: #008fff;
           border-radius: 4px;
           font-family: PingFangSC-Regular;
           font-size: 18px;
           font-weight: normal;
           font-stretch: normal;
           line-height: 44px;
           letter-spacing: 0px;
           color: #ffffff;
           text-align: center;
           cursor: pointer;
         }
  }
  .info-r2 {
    box-sizing: border-box;
    padding: 21px 25px 59px 35px;
  }
.info-r-1{
  height: 32px;
  line-height: 32px;
  border-top: 1px dashed #bcbcbc;
  border-bottom: 1px dashed #bcbcbc;
  color: #1e58a3;
  font-size: 12px;
   span {
     display: inline-block;
     width: 49%;
     text-align: center;

   }
  span:nth-child(1){
    border-right: 1px dashed #bcbcbc;

  }
}
.info-r-2 {
  text-align: center;
  box-sizing: border-box;
  padding-top: 22px;
  padding-bottom: 21px;
    img {
      width: 100px;
      height: 100px;
    }
}
  .info-r-3 {
    color: #393939;
    font-size: 16px;
    text-align: center;
  }
.info-r-4 {
  color: #797979;
  font-size: 14px;
  text-align: center;
  margin-top: 15px;
  position: relative;
  img {
    position: absolute;
    left: 14px;
    top: 0;
    width: 17px;
    height: 21px!important;
  }
}
  .infoDetails {
    width: 1200px;
    height: 660px;
    margin: 21px auto;
    background-color: #ffffff;
    border: solid 1px #bcbcbc;
  }
  .detail-head {
    height: 57px;
    line-height: 57px;
    background-color: #f8f8f8;
    span {
      display: inline-block;
    }
    span:nth-child(1){
      width: 191px;
      text-align: center;
    }
    span:nth-child(2){
      width: 168px;
      text-align: center;
    }
  }
  .active2 {
    background-color: #ffffff;
    border-top: 3px solid #008fff;
  }
.detail-content {
  box-sizing: border-box;
  height: 603px;
  width: 100%;
  color: #008fff;
  font-size: 18px;
  padding: 68px 49px 60px 63px;
  div {
    font-size: 16px;
    color: #8f8f8f;
    line-height: 24px;
    font-family: PingFangSC-Semibold;
    font-weight: 500px;
  }
  div:nth-child(1){
   margin-bottom: 20px;
  }
}
  .share {
    position: absolute;
    right: 0;
    top: 0;
    color: #bebebe;
    font-size: 12px;
      i {
        font-style: normal;
        vertical-align: middle;
        margin-right: 11px;
      }
    img {
      vertical-align: middle;
      margin-right: 14px;
    }
  }
</style>



